import AppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import { useState } from "react";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
// import { login, logout } from "./Redux/features/authslice";
export function Header() {
  const { isloggedIn } = useSelector((state) => {
    return state.authreducer;
  });
  console.log("storrr", isloggedIn);
  const [value, setValue] = useState();
  return (
    <>
      <AppBar
        position="sticky"
        sx={{
          background:
            "linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(121,63,9,1) 74%, rgba(0,212,255,1) 100%);"
        }}
      >
        <Toolbar>
          <Typography variant="h4">Favourite cities</Typography>
          {isloggedIn && (
            <Box display="flex" marginLeft="auto" marginRight="auto">
              <Tabs
                textColor="inherit"
                value={value}
                onChange={(e, val) => {
                  return setValue(val);
                }}
              >
                <Tab LinkComponent={Link} to="/cities" label="All Cities" />
                <Tab LinkComponent={Link} to="/mycities" label="My Cities" />
              </Tabs>
            </Box>
          )}
          <Box display="flex" marginLeft="auto">
            <Button
              LinkComponent={Link}
              to="/auth"
              variant="contained"
              sx={{
                margin: 1,
                borderRadius: 15
              }}
              color="warning"
            >
              Login
            </Button>
            <Button
              LinkComponent={Link}
              to="/auth"
              variant="contained"
              sx={{ margin: 1, borderRadius: 15 }}
              color="warning"
            >
              Signup
            </Button>

            {isloggedIn && (
              <Button
                LinkComponent={Link}
                to="/auth"
                variant="contained"
                sx={{ margin: 1, borderRadius: 15 }}
                color="warning"
              >
                Logout
              </Button>
            )}
          </Box>
        </Toolbar>
      </AppBar>
    </>
  );
}

